import React from "react";
import Kankou from "./kankou";
import "./styles.css";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import TwitterIcon from "@material-ui/icons/Twitter";
import PinDropIcon from "@material-ui/icons/PinDrop";
import LanguageIcon from "@material-ui/icons/Language";
import Avatar from "@material-ui/icons/Grade";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  avatar: {
    backgroundColor: red[500]
  }
}));

function RecipeReviewCard() {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const items = [];
  for (let i = 0; i < Kankou.length; i++) {
    items.push(
      <div>
        <Card className={classes.root}>
          <CardHeader
            avatar={
              <Avatar aria-label="recipe" className={classes.avatar}></Avatar>
            }
            title={Kankou[i]["名前"]}
          />
          <CardMedia
            className={classes.media}
            image={Kankou[i]["写真"]}
            title={Kankou[i]["名前"]}
          />
          <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
              {Kankou[i]["説明"]}
            </Typography>
          </CardContent>

          <IconButton
            href="https://twitter.com/share?url=https://kato00.github.io/csb-ld1v17/#Kan"
            target="_blank"
          >
            <TwitterIcon />
          </IconButton>
          <a
            href="http://line.me/R/msg/text/?https://kato00.github.io/csb-ld1v17/#Kan%0a愛知県のおすすめ"
            target="_blank"
          >
            LINE
          </a>
          <IconButton href={Kankou[i]["map"]} target="_blank">
            <PinDropIcon />
          </IconButton>
          <IconButton href={Kankou[i]["web"]} target="_blank">
            <LanguageIcon />
          </IconButton>
        </Card>
        <br />
      </div>
    );
  }

  return items;
}

export default function () {
  return <div>{RecipeReviewCard()}</div>;
}
